@import url(https://fonts.googleapis.com/css2?family=Mulish:ital,wght@0,500;0,700;1,600&family=Sansita+Swashed:wght@300&display=swap);
* {
  font-family:'Times New Roman', Times, serif;
  
}
.navbar {
  background-color: transparent !important;
}
.navbar-brand {
  font-size: 1.9rem !important;
  color: #565387 !important;
}
.navbar a {
  font-size: 1.3rem;
  text-transform: capitalize;
}

.menu_active {
  font-weight: bold;
  border-bottom: 1px solid #565387;
}

.navbar a:hover {
  color: #3498db !important;
}

#header .brand-name {
  color: #3498db;
}
#header {
  width: 100%;
  height: 80vh;
  margin-top: 70px;
}

#herder h1 {
  margin: 0 0 10px 0;
  font-size: 48px;
  font-weight: 700;
  color: black;
}

#header h2 {
  color: #484848;
  font-size: 24px;
}
#header .btn-outline-primary {
  font-weight: 500;
  font-size: 16px;
  letter-spacing: 1px;
  display: inline-block;
  padding: 10px 30px;
  border-radius: 50px;
  transition: 0.5s;
  color: #3498db !important;
  border: 2px solid #3498db;
  text-decoration: none;
}

#header .btn-outline-primary:hover {
  background: #3498db;
  color: #fff !important;
}

.header-img {
  text-align: right;
}

#header .animated {
  animation: up-down 2s ease-in-out infinite alternate-reverse both;
}

@keyframes up-down {
  0% {
    transform: translateY(10px);
  }
  100% {
    transform: translateY(-10px);
  }
}

footer p {
  padding: 0;
  margin: 0;
}

@media (max-width:991px){
  #header{
    height: 80vh;
    text-align: center;
  }
  #header .header-img {
    text-align: center;
  }
  #header .header-img img{
    width: 60%;
  }
}

@media (max-width: 768px){
  #header{
    margin-top:20px;
  }
  #header h1{
    font-size: 28px;
  }
  #header h2{
    font-size: 18px;
    margin-bottom: 30px;
  }
  #header  .header-img img{
    width:70%;
  }
}
